import React from "react";
import PropTypes from "prop-types";
import CssBaseline from "@material-ui/core/CssBaseline";
// import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import withStyles from "@material-ui/core/styles/withStyles";

import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import { withLocalization } from "../shared/localization";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import config from "../../config";
import classNames from "classnames";
import getSharedStyles from "../../style";
import CircleLoader from "../dobyComponents/CircleLoader";

const styles = (theme) => ({
    ...getSharedStyles(theme),   
    
    logoImg: {
        height: '25px'
    },
   
});

let _code, _provider, _mutex, _stripeId;
class OAuthComponent extends React.Component {
    constructor(props) {
        super(props);

        const urlParams = new URLSearchParams(this.props.location.search);
        _provider = urlParams.get("provider");
        _code = urlParams.get("code");
        _stripeId = urlParams.get("id");
    }

    async componentDidMount() {
        //NOTE: redirecting from root to verification of payment
        //when we are getting back from stripe
        if (_stripeId) {
            this.props.history.replace(`/eshop/checkout/verify?id=${_stripeId}`);
            return;
        }

        if (_provider && !_mutex) {
            _mutex = true;
            const { user } = this.props;
            try {
                if (user.user) {
                    await user.updateOauth(_provider, _code);
                    this.props.history.replace("/account");
                } else {
                    if (!(await user.oAuth(_provider, _code))) {
                        user.showMsg("Unauthorized");
                    }
                    this.props.history.replace(config.home);
                }
            } catch (error) {
                this.props.user.showError(error);
                this.props.history.replace("/login");
            }
            _mutex = false;
        }
    }

    render() {
        const { classes, translate } = this.props;

        if (!_provider) return <Redirect to={{ pathname: config.home }} />;
        return (
            <>
                <div className={classNames(classes.container, classes.mt10)}>
                    <div className={classNames(classes.dFlex, classes.flexCol, classes.justifyContentBetween)}>
                        <div className={classNames(classes.dFlex, classes.alignItemsStart, classes.justifyContentCenter)}>
                            <img className={classes.logoImg} src={`/img/${config.locale}/logo.png`} alt="logo" />
                        </div>
                        <CircleLoader center className={classNames(classes.pt8, classes.pb5)} />
                        <div className={classNames(classes.textMono, classes.textGray300, classes.fontMedium, classes.textCenter)}>
                            {translate("loading")}
                        </div>
                    </div>
                </div>
            </>
            //   <main className={classes.main}>
            //     <CssBaseline />
            //     <Paper className={classes.paper}>
            //       <img className={classes.avatar} src={`/img/${config.locale}/logo.png`} alt="logo" />
            //       <div className={classes.loader}>
            //         <Typography component="h1" variant="h5">
            //           {translate('loading')}
            //         </Typography>
            //       </div>
            //     </Paper>
            //   </main>
        );
    }
}

OAuthComponent.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(withStyles(styles), withUser, withStore, withLocalization)(OAuthComponent);
