import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import FormDialog from "../shared/FormDialog";
import Form from "../shared/form/Form";
import Grid from "@material-ui/core/Grid";
import classNames from "classnames";
import Button from "@material-ui/core/Button";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import withUtils from "../shared/utils/withUtils";
import compose from "../../compose";
import TextField from "../dobyComponents/form/TextField";
import { getStyles } from "../shared/style";
import { withLocalization } from "../shared/localization";
import _ from "lodash";

const styles = (theme) => ({
  ...getStyles(theme),
  heading: {
    marginLeft: theme.spacing.unit,
  },
  detailHeading: {
    marginLeft: theme.spacing.unit,
    marginTop: theme.spacing.unit * 3,
  },
  actions: {
    float: "right",
    margin: theme.spacing.unit,
  },
  submitButton: {
    margin: theme.spacing.unit,
    marginBottom: theme.spacing.unit * 2,
  },
});

const INIT_STATE = {
  email: {
    value: "",
    isLoading: false,
    errorText: "",
  },
  isOpen: true,
};

class FormWrapper extends React.Component {
  state = INIT_STATE;

  handleChange = (name, value) => {
    this.setState((state) => _.set(state, name, value));
  };

  isValid = async (username) => {
    const { user } = this.props;

    const ids = await this.props.user.checkUser(username);
    return (
      ids.length === 0 || (ids.length === 1 && ids[0]._id === user.user._id)
    );
  };

  validEmail = async () => {
    const { translate } = this.props;
    const { email } = this.state;

    this.setState((state) => ({
      email: {
        ...state.email,
        isLoading: true,
      },
    }));

    const isValid = !email.value || (await this.isValid(email.value));
    const errorText = isValid ? "" : translate("field.emailInUse");

    this.setState((state) => ({
      email: { ...state.email, errorText, isLoading: false },
    }));

    return isValid;
  };

  handleSubmit = async () => {
    const { email } = this.state;

    const isValid = await this.validEmail();
    if (!isValid) {
      return;
    }

    // try {
    //   const resetObject = {
    //     username: email.value,
    //     redirectUri: process.env.REACT_APP_HOMEPAGE,
    //     from: config.mail.address,
    //     fromName: config.mail.name,
    //     subject: this.props.translate("email_reset_password"),
    //     templateId: config.mail.resetPasswordTemplate,
    //   };

    //   await this.props.user.reset(resetObject);
    //   this.props.user.showMsg("Mail sended.");
    // } catch (error) {
    //   this.props.user.showError(error);
    // }
  };

  render() {
    const { classes, translate, isOpen, onClose } = this.props;
    const { email } = this.state;

    return (
      <FormDialog
        title={translate("forgottenPassword")}
        isOpen={isOpen}
        onClose={onClose}
      >
        {/* <Form onSubmit={this.handleSubmit} inline> */}
        <Grid container direction="row" justify="center" alignItems="center">
          <Grid item xs={12} className={classNames(classes.mt4)}>
            <TextField
              onChange={this.handleChange}
              fullWidth
              required
              value={email.value}
              field="email.value"
              type="email"
              onBlur={this.validEmail}
              errorText={email.errorText}
            />
            <Button
              className={classes.submitButton}
              type="submit"
              variant="contained"
              color="primary"
            >
              {translate("change")}
            </Button>
          </Grid>
        </Grid>
        {/* </Form> */}
      </FormDialog>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default compose(
  withStyles(styles),
  withStore,
  withUser,
  withUtils,
  withLocalization
)(FormWrapper);
