import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import RedTriangle from "./icons/RedTriangle";
import ArrowRight from "./icons/ArrowRight";
import withDoby from "../../consumers/withDoby";

import Stopwatch from "../dobyComponents/icons/Stopwatch";
import Altitude from "../dobyComponents/icons/Altitude";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    summitInfo: {
        background: "white",
    },
    photoHolder: {
        //width: '100%',
        width: "calc(100% - 25px)",
        minHeight: "200px",
        paddingTop: '75%'
    },
});

function SummitCard(props) {
    const { classes } = props;
    const [img, setImg] = useState(null);

    useEffect(() => {
        const getImg = async () => {
            try {
                if (props.thumbnailId) {
                    const data = await props.doby.getFile(props.thumbnailId);
                    var reader = new FileReader();
                    reader.onloadend = () => {
                        setImg(reader.result);
                    };
                    reader.readAsDataURL(data);
                }
            } catch (e) {
                console.error(e);
            }
        };

        if (!img) {
            getImg();
        }
    }, []);

    return (
        <>
            <div className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsEnd)}>
                <div className={classNames(classes.dFlex, classes.w100, classes.summitInfo, classes.p3, classes.alignItemsCenter)}>
                    <RedTriangle size={20} />
                    <div className={classNames(classes.dFlex, classes.flexCol, classes.ml4)}>
                        <h5 className={classNames(classes.textMono, classes.fontSemibold)}>{props.title}</h5>
                        <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.textM, classes.mt1)}>
                            <Altitude size={16} className={classNames(classes.mr1, classes.textGray500)} />
                            <div className={classNames()}>{props.elevation} mnm</div>
                            <Stopwatch size={16} className={classNames(classes.ml4, classes.mr1, classes.textGray500)} />
                            <div className={classNames()}>{props.time} h</div>
                        </div>
                    </div>
                    {/* <ArrowRight size={22} className={classNames(classes.mlAuto, classes.textSecondary)}/> */}
                </div>
                <div className={classNames(classes.dFlex, classes.w100, classes.photoHolder, classes.imageFitHolder)}>
                    <img src={img} className={classNames(classes.imageFit)} alt="" />
                </div>
            </div>
        </>
    );
}

export default compose(withStyles(styles), withLocalization, withStore, withDoby)(SummitCard);
