import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";
import Flickity from "react-flickity-component";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    sliderDragged: {
        '& .flickity-viewport': {
            pointerEvents: 'none',           
        },
    },
    carousel: {
        outline: 'none!important',
        border: '0'
    }
});

const flickityOptions = {
    cellAlign: "center",
    contain: true,
    prevNextButtons: false,
    pageDots: false,
    dragThreshold: 5,
};

class Slider extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sliderDragged: false, 
        };
        this.challengesSliderRef = React.createRef()
    }
    componentDidMount() {      
        if(this.challengesSliderRef && this.props.children.length > 0) {
            this.challengesSliderRef.current.flkty.on("dragStart", () => {
                //this.setState({ sliderDragged: true });
                this.challengesSliderRef.current.flkty.slider.childNodes.forEach(slide => slide.style.pointerEvents = "none")
            });
            this.challengesSliderRef.current.flkty.on("dragEnd", () => {
                // setTimeout(() => {
                //     this.setState({ sliderDragged: false });
                // }, 200);
                this.challengesSliderRef.current.flkty.slider.childNodes.forEach(slide => slide.style.pointerEvents = "all")
            });
        }
    } 


    render() {
        const { classes, className, options, children } = this.props;
        return (
            <>
                { children.length > 0 &&
                    <Flickity
                        className={classNames(className, classes.carousel, this.state.sliderDragged && classes.sliderDragged)}
                        options={options || flickityOptions}
                        reloadOnUpdate={false}
                        ref={this.challengesSliderRef}>
                        {children}
                    </Flickity>
                }
            </>
        );
    }
}

export default compose(withStyles(styles), withLocalization)(Slider);
